export default {
  name: "Intro",
  head () {
    return {
      title: 'Amanda Graham - About Me',
    }
  },
  async asyncData({ $prismic, error }) {
    try {
      const intro = (await $prismic.api.getSingle('intro')).data

      return {
        intro
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
}
